'use client'

import { getRole } from "@/services/getRole";
import { IPermission, IRole } from "@/type/Types";
import { useEffect, useState } from "react";
import styles from './role.module.scss';

export default function RoleClient() {
    const [roles, setRoles] = useState<IRole[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchRoles = async () => {
          try {
            const data = await getRole();
            setRoles(data);
          } catch (error) {
            console.error('Ошибка загрузки ролей:', error);
            setRoles([]);
          } finally {
            setIsLoading(false);
          }
        };
        fetchRoles();
      }, []);

    if (isLoading) {
        return <div className={styles.loading}>Загрузка ролей и разрешений...</div>;
    }

    return (
        <div className={styles.container}>
            <h1 className={styles.title}>Роли и разрешения</h1>
            <div className={styles.tableContainer}>
                <table className={styles.table}>
                    <thead>
                        <tr>
                            <th>Название роли</th>
                            <th>Разрешения</th>
                            <th>Действия</th>
                        </tr>
                    </thead>
                    <tbody>
                        {roles.map((role, index) => (
                            <tr key={index} className={styles.tableRow}>
                                <td className={styles.roleNameCell}>
                                    <span className={styles.roleBadge}>{role.name}</span>
                                </td>
                                <td>
                                    <div className={styles.permissionsContainer}>
                                        {role.permissions && Array.isArray(role.permissions) ? (
                                            <div className={styles.permissionsList}>
                                                {role.permissions.map((permission: string | IPermission, index) => (
                                                    <span key={index} className={styles.permissionTag}>
                                                        {typeof permission === 'string' 
                                                            ? permission 
                                                            : permission.name || JSON.stringify(permission)}
                                                    </span>
                                                ))}
                                            </div>
                                        ) : (
                                            <span className={styles.noPermissions}>Нет разрешений</span>
                                        )}
                                    </div>
                                </td>
                                <td className={styles.actionsCell}>
                                    <button className={styles.actionButton}>
                                        Редактировать
                                    </button>
                                    <button className={`${styles.actionButton} ${styles.deleteButton}`}>
                                        Удалить
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}